div {
	text-align: center;
	border-style: solid;
    border-color: darkblue;
}


body {
    background-color: gray;
}

nav {
    background-color: dodgerblue;
    margin: 30px; 
    padding: 10px;
    max-width: 5%;
    border-style: solid;
    border-color: darkblue;
    text-align: center;

    position: sticky;
    top: 0;
}

nav p {
	font-size: 13px;
}

header {
	font-size: 70px;
}

img {
	width: 100%;
	height: auto;
}



p {
	font-size: 40px;
	border-style: solid;
    border-color: purple;
	text-align:center; 
    margin-left:auto; 
    margin-right:auto; 
}

.membersTable {
	border-collapse: collapse;
	border-style: solid;
    border-color: purple;
	text-align: center;
}

div .AdminTable {
	text-align:center; 
    margin-left:auto; 
    margin-right:auto; 
    width:200px;
}	

th, td {
	border-style: solid;
    border-color: purple;
}

footer {
	font-size: 20px;
}









